export default "/* Quantity Button */\ninput,\ntextarea {\n  border: 1px solid #eeeeee;\n  box-sizing: border-box;\n  margin: 0;\n  outline: none;\n  padding: 10px;\n}\ninput[type=button] {\n  -webkit-appearance: button;\n  cursor: pointer;\n}\ninput::-webkit-outer-spin-button,\ninput::-webkit-inner-spin-button {\n  -webkit-appearance: none;\n}\n.input-group {\n  clear: both;\n  margin: 15px 0;\n  position: relative;\n}\n.input-group input[type=button] {\n  background-color: #eeeeee;\n  min-width: 38px;\n  width: auto;\n  transition: all 300ms ease;\n}\n.input-group .button-minus,\n.input-group .button-plus {\n  font-weight: bold;\n  height: 38px;\n  padding: 0;\n  width: 38px;\n  position: relative;\n}\n.input-group .quantity-field {\n  position: relative;\n  height: 38px;\n  left: -6px;\n  text-align: center;\n  width: 62px;\n  display: inline-block;\n  font-size: 13px;\n  margin: 0 0 5px;\n  resize: vertical;\n}\n.button-plus {\n  left: -13px;\n}\ninput[type=number] {\n  -moz-appearance: textfield;\n  -webkit-appearance: none;\n}\n/* Quantity Button */\n/*# sourceMappingURL=data:application/json;base64, */"