import { Component, OnInit } from '@angular/core';
import {CategoryService} from '../../../shared/services/category/category.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

  account = sessionStorage.getItem('c_name');
  categories = [];

  constructor(
    private categoryService: CategoryService
  ) {
    this.getCategories();
  }

  ngOnInit() {
  }

  logout() {
    sessionStorage.removeItem('c_jwt');
    sessionStorage.removeItem('c_role');
    sessionStorage.removeItem('c_name');
    sessionStorage.removeItem('jwt');
    window.location.reload();
  }

  getCategories() {
    this.categoryService.getAllSubcategories()
      .subscribe(serverResponse => {
        if (serverResponse) {
          if (serverResponse.response) {
            this.categories = serverResponse.data.subCategories;
            //console.log(serverResponse.data.subCategories);
          } else {
            console.log(serverResponse.error);
          }
        } else {
          console.log('error');
        }
      });
  }
}
